.nav{
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  a{
    color: white;
    font-size: 20px;
  }
  &__item-under{
    width: 100%;
    text-align: center;
  }
  &__link-under{
    width: 100%;
    display: block;
    padding: 10px;
  }
  &__link{
    &-under{
      text-decoration: none;
    }
  }
  &__list{
    background-color: rgba(8, 4, 4, 0.61);
    position: absolute;
    width: 90%;
    height: max-content;
    padding: 20px 0;
    left: -1300px;
    top: 50px;
    transition: 0.3s;
    justify-content: flex-start;
    display: flex;
    flex-direction: column;
    align-items: center;
    &-link{
      width: 100%;
      text-align: center;
      text-decoration: none;
      margin-bottom: 10px;
    }
    &-item{
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      min-height: 30px;
      margin-top: 10px;
    }
    &.active{
      left: 20px;
      transition: 0.3s;
    }
    &-under{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      max-height: 0px;
      overflow: hidden;
      width: 100%;
      transition: 0.5s;
      background-color: rgba(146, 15, 15, 0.8);
      &.active{
        min-height: max-content;
        max-height: 300px;
        transition: 0.5s;  
      }
    }
  }
}

.burger{
  position: relative;
  z-index: 11;
  width: 35px;
  height: 2px;
  &-span1{
    content: "";
      background-color: red;
      position: absolute;
      width: 35px;
      height: 2px;
      left: 0;
      bottom: -1px;
      transform: translateY(7px);
      transition: 0.3s;
    &.active{
      transform: rotate(45deg);
      transition: 0.3s;
    }
  }
  &-span2{
    content: "";
    background-color: red;
    position: absolute;
    width: 35px;
    height: 2px;
    left: 0;
    top: -1px;
    transform: translateY(-7px);
    transition: 0.3s;
    &.active{
      transform: rotate(-45deg);
      transition: 0.3s;
      top: 1px;
    }
  }
  &-span3{
    content: "";
    background-color: red;
    position: absolute;
    width: 35px;
    height: 2px;
    left: 0;
    top: 7px;
    transform: translateY(-7px);
    transition: 0.3s;
    &.active{
      transform: rotate(-45deg);
      transition: 0.3s;
      top: 0;
      height: 0px;
    }
  }
}



@media (min-width: 800px) {
  .burger{
    display: none;
  }
  .burgermenu{
    width: 100%;
  }
  .nav{
    width: 100%;
    a{
      font-size: 17px;
    }
    &__item-under{
 
    }
    &__list{
      display: flex;
      flex-direction: row;
      position: relative;
      align-items: center;
      position: static;
      min-width: 500px;
      padding: 0px;
      min-height: 30px;
      width: 100%;
      background-color: black;
      &-link{
        color: white;
        margin-bottom: 0px;
        margin-top:3px;
      }
      &-item {
        position: relative;
        margin-top: 0px;
        // height: 50px;
        display: flex;
        align-items: center;
        height: 100%;
        &:hover{
          background-color: rgba(146, 15, 15, 1);
        }
        &:hover ul{
          top: 30px;
          display: flex !important;
          align-items: center;
          justify-content: center;
          left: 0;
          position: absolute;
          min-height: max-content !important;
          max-height: 300px;
          min-width: max-content;
        }
      }
      &-under{
        display: none;

        li{
          width: 100%;
          text-align: center;
          padding: 10px 0;
          &:hover{
            background-color: red;
          }
        }
        a{
          color: white;
          padding: 5px;
        }
      }
    }
  }
}
