.tools{
  margin-top: 20px;
  &__list{
    display: grid;
    grid-template-columns: 4fr 4fr;
    grid-auto-rows: minmax(50px, auto);
    &-item{
      padding: 5px;
    }
    &-link{
      display: flex;
      flex-direction: column;
      align-items: center;
      text-decoration: none;
      text-align: center;
    }
    &-img{
      width: 68px;
      height: 70px;
    }
    &-name{
      font-size: 15px;
      margin-top: 10px;
      max-width: 90px;
    }
  }
}

@media (min-width: 800px){
  .tools{
    margin-top: 50px;
    &__list{
      display: grid;
      grid-template-columns: 4fr 4fr 4fr 4fr;
      grid-auto-rows: minmax(50px, auto);
      &-link{
        padding: 0 10px ;
      }
      &-link:hover{
        background-color: rgba(240, 238, 238, 0.514);
      }
      &-item{
        border-right: 1px solid gray;
        padding: 5px 20px;
        &:last-child{
          border: 0px;
        }
      }
    }
  }
}