.header{
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  background-color: white;
  &__main{
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    align-items: center;
    min-height: 50px;
    width: 100%;
  }
  &__logotype{
    width: 110px;
    &-img{
      width: 100%;
    }
  }
  &__userroom{
    display: flex;
    &-link{
      display: flex;
      text-decoration: none;
      text-align: center;
      padding: 7px 10px;
      align-items: center;
      background-color: red;
      border-radius: 15px;
      color: white;
      font-weight: 600;
      width: max-content;
      overflow: hidden;
      font-size: 11px;
      border: 1px solid wheat;
      &:hover{
        background-color: rgb(221, 43, 43);
        transition: .3s;
      }
    }
    &-img{
      width: 15px;
      margin-right: 7px;
    }
  }
  &__service{
    display: none;
    &-item{
      border-right: 1px solid gray;
      &:last-child{
        border-right: 0px;
      }
    }
    &-img{
      width: 30px;
      &:hover{
        filter: saturate(300%)
      }
    }
    &-list{
      display: flex;
    }
    &-link{
      display: flex;
      flex-direction: column;
      align-items: center;
      text-decoration: none;
      padding: 5px 20px;
      color: grey;
      &:hover{
        color: black;
      }
    }
  }
}


@media (min-width: 800px) {
  .header{
    display: flex;
    flex-direction: column;
    &__main{
      justify-content: space-between;
    }
    &__logotype{
      width: 150px;
    }
    &__service{
      display: flex;
      &-img{ 
        width: 40px;
      }
    }
  }
}

@media (min-width: 550px) {
  .header{
   &__userroom{
    &-link{
      display: flex;
      text-decoration: none;
      padding: 10px;
      align-items: center;
      background-color: red;
      border-radius: 0px;
      color: white;
      font-weight: 600;
      width: 160px;
      overflow: hidden;
      font-size: 12px;
      border: 1px solid wheat;
      &:hover{
        background-color: rgb(221, 43, 43);
        transition: .3s;
      }
    }
   }
  }
}