.main{
  &__title{
    background-image: url(./../img/title-bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    &-h1{
      color: #00456A;
      margin-bottom: 10px;
    }
    &-wrapper{
      background-color: rgba(255, 255, 255, 0.226);
      width: 100%;
      padding-right: 30%;
    }
    &-text{
      width: 70%;
      font-size: 15px;
      margin-bottom: 10px;
      font-weight: 600;
    }
    &-link{
      display: flex;
      text-decoration: none;
      text-align: center;
      padding: 10px 10px;
      align-items: center;
      background-color: red;
      color: white;
      font-weight: 600;
      width: max-content;
      overflow: hidden;
      margin-top: 10px;
      margin-bottom: 10px;
      font-size: 15px;
      border: 1px solid wheat;
      &:hover{
        background-color: rgb(221, 43, 43);
        transition: .3s;
      }
    }
  }
}

@media (min-width: 800px){
  .main{
    &__title{
      min-height: 400px;
      &-wrapper{
        min-height: 400px;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
    }
  }
}