.tarifs{
  margin-top: 50px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.pricetarif{
  color: red;
  font-weight: 600;
}
.item{
  margin-bottom: 30px;
  max-width: 280px;
  box-shadow: 0 1px 2px rgba(0,0,0,.24), 0 1px 3px rgba(0,0,0,.12);
  &:hover{
    background-color: #f8f8f8;
  }
}
.main{
  h3{
    color: #00456A;;
    margin-bottom: 15px;
    margin-top: 15px;
  }
  padding: 10px;
 
}
.title{
  background-color: rgb(231, 57, 57);
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 5px;
  min-height: 170px;
  justify-content: space-around;
  span{
    color: white;
    font-size: 20px;
  }
}
.list{
  margin-top: 30px;
}
.link{
  display: flex;
  text-decoration: none;
  text-align: center;
  padding: 7px 10px;
  align-items: center;
  background-color: #00456A;
  color: white;
  font-weight: 600;
  width: max-content;
  overflow: hidden;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 10px;
  border: 1px solid wheat;
  &:hover{
    background-color: rgb(20, 118, 170);
    transition: .3s;
  }
}

@media (min-width: 800px){
  .list{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .item{
    max-width: 250px !important;
  }
}

@media (min-width: 900px){
  .item{
    max-width: 260px !important;
  }
}
@media (min-width: 1000px){
  .item{
    max-width: 300px !important;
  }
}