.footer{
  background-color: #313131;
  display: flex;
  flex-direction: column;
  padding: 10px 20px;
  min-height: 450px;
  align-items: center;
  span{
    color: white;
  }
}
.contact{
  margin-bottom: 10px;
}
.dom{
  margin-bottom: 10px;
}
.contacts{
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}
.mapwrapper{
  width: 100%;
  height: 100%;
}
.map{
  width: 100%;
  height: 450px;
}

@media (min-width: 650px){
.footer{
  display: flex;
  flex-direction: row;
}
}